#box{
    position: relative;
    overflow: hidden;
}
.content
{
    color: #fff;
    /* background: rgba(142, 149, 173, 0.9); */
    background: linear-gradient(rgba(58, 92, 206, 0.9),rgba(192, 201, 231, 0.9));
    position:absolute;
    top:-100%;
    left:0;
    width: 100%;
    height: 65%;
    padding: 20px;
    box-sizing: border-box;
    /* display: none; */
    transition: all 0.5s;
}
#box:hover .content{
    top:0;
}